<template>
  <nav-bar>
    <span slot="title" class="title">活动管理 - {{coupon.title}}</span>
  </nav-bar>
  <table class="table">
    <tr>
      <td style="width:200px;padding-left:50px;">
        <img @click="inputFileClick" alt="店铺头像" class="img-circle" width="100" height="100" v-bind:src="coupon.avatar" />
        <input @change="uploadAvatar" v-el:avatar class="avatar" type="file" accept="image/*" style="display:none;" />
      </td>
      <td colspan="2">
        <div class="content-form">
          <form @submit="update" accept-charset="UTF-8" method="post">
            <div class="form-group row">
              <label for="inputEmail3" class="col-sm-2 form-control-label">主题</label>
              <div class="col-sm-10">
                <input class="form-control" placeholder="主题" type="text" v-model="coupon.title" />
              </div>
            </div>
            <div class="form-group row">
              <label for="inputEmail3" class="col-sm-2 form-control-label">详情</label>
              <div class="col-sm-10">
                <textarea class="form-control" rows="3" v-model="coupon.details" placeholder="详情"></textarea>
              </div>
            </div>
            <div class="form-group row">
              <label for="inputEmail3" class="col-sm-2 form-control-label">原价格</label>
              <div class="col-sm-10">
                <input class="form-control" placeholder="奖品数量" type="text" v-model="coupon.original_price" />
              </div>
            </div>
            <div class="form-group row">
              <label for="inputEmail3" class="col-sm-2 form-control-label">优惠价格</label>
              <div class="col-sm-10">
                <input class="form-control" placeholder="奖品数量" type="text" v-model="coupon.preferential_price" />
              </div>
            </div>
            <div class="form-group row">
              <label for="inputEmail3" class="col-sm-2 form-control-label"></label>
              <div class="col-sm-10">
                <input type="submit" name="commit" value="保 存" class="btn btn-primary" />
              </div>
            </div>
          </form>
        </div>
      </td>
    </tr>
  </table>
  <table class="table">
    <tr>
      <td colspan="2">
        参加活动的用户
      </td>
    </tr>
    <tr v-for="coupon_user in coupon.coupon_users">
      <td>
        {{coupon_user.user.nickname}}
      </td>
      <td>
        {{coupon_user.used}}
      </td>
      <td>
        {{coupon_user.used_at}}
      </td>
    </tr>
  </table>
</template>
<script>
  export default {
    data () {
      return {
        coupon: {
          title: '',
          details: '',
          original_price: 0,
          preferential_price: 0,
          avatar: ''
        }
      }
    },
    route: {
      data (transition) {
        var params = this.$route.params
        this.$http.get('http://jiancan.me/api/m1/coupons/one.json', { access_token: localStorage.token, coupon_id: params.coupon_id }).then(function (response) {
          this.$set('coupon', response.data)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    },
    methods: {
      inputFileClick (e) {
        return this.$els.avatar.click()
      },
      uploadAvatar (e) {
        e.preventDefault()

        if (this.$els.avatar.value === '') return
        var formData = new FormData()
        formData.append('access_token', localStorage.token)
        formData.append('coupon_id', this.$route.params.coupon_id)
        formData.append('coupon[avatar]', this.$els.avatar.files[0])

        this.$http.put('http://jiancan.me/api/m1/coupons/one.json', formData).then(function (response) {
          this.$set('coupon', response.data.coupon)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      },
      update (e) {
        e.preventDefault()

        var formData = new FormData()
        formData.append('access_token', localStorage.token)
        formData.append('coupon_id', this.$route.params.coupon_id)
        formData.append('coupon[title]', this.coupon.title)
        formData.append('coupon[details]', this.coupon.details)
        formData.append('coupon[original_price]', this.coupon.original_price)
        formData.append('coupon[preferential_price]', this.coupon.preferential_price)

        this.$http.put('http://jiancan.me/api/m1/coupons/one.json', formData).then(function (response) {
          this.$set('coupon', response.data.coupon)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue')
    }
  }
</script>



